.header-wrap{
	padding:1rem 2.5rem 1rem 2.5rem !important;
	border:1px red solid;
	align-items: center;
	display: flex;
	flex-wrap: wrap;
	height:64px;
}
.icon-wrap{
	box-sizing: border-box;
	display: flex;
	width:36px;
	height:24px;
	/* padding-bottom:.75rem !important; */
	justify-content: space-between !important;
	align-items: center;
	border:1px solid pink;
}
.icon-wrap a{
	display: block;
	width:24px;
	height:24px;
}
.icon-wrap a img{
	width:24px;
	height:24px;
}